var abs = Math.abs;
module.exports = closest;

function closest(n, arr, rndx) {
  var i,
      ndx,
      diff,
      best = Infinity;
  var low = 0,
      high = arr.length - 1;

  while (low <= high) {
    i = low + (high - low >> 1);
    diff = arr[i] - n;
    diff < 0 ? low = i + 1 : diff > 0 ? high = i - 1 : void 0;
    diff = abs(diff);
    if (diff < best) best = diff, ndx = i;
    if (arr[i] === n) break;
  }

  return rndx ? ndx : arr[ndx];
}